import React from 'react';
import styled from 'styled-components';
import Icon from '../../assets/Icon';
import { Container, TitleItem } from './AppQuizHeader';

const Figure = styled.div`
    width: 120px;
    height: 80px;
    border-radius: 2px;
    overflow: hidden;
`;

const ItemContainer = styled(Container)`
    background: white;
`

const Image = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
`;

const Input = styled.input`
    margin-right: 18px;
    cursor: pointer;
`

const Item = styled(TitleItem)`
    display: flex;
    align-items: center;
`

const Answer = styled.div`
    width: 240px;
    height: 30px;
    align-items: center;
    display: flex;
    margin-bottom: 10px;
    padding: 2px 0;
`;

const Number = styled.div`
   width: 24px;
   height: 24px;
   background: ${props => props.correct ? 'green' : '#707070'};
   margin-right: 5px;
   display: flex;
   justify-content: center;
   align-items: center;
   color: white;
`;

const AnswerItem = styled(TitleItem)`
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
`

export default function AppQuizItem({ question }) {
    return (
        <ItemContainer>
            <AnswerItem tile={1.5}>
                <Input type="checkbox" />
                <Figure>
                    <Image src={question.image} alt="" />
                </Figure>
            </AnswerItem>
            <AnswerItem tile={5}>
                <Answer>
                    <Number correct>1</Number>
                    {question.title}
                </Answer>
                {question.answers.map((answer, index) =>
                    <Answer key={answer}>
                        <Number>{index + 1}</Number>
                        {answer}
                    </Answer>
                )}
            </AnswerItem>
            <Item tile={3}>
                {question.tags.map(tag => <span key={tag} style={{ marginRight: 5 }}>{tag},</span>)}
            </Item>
            <Item tile={.5} style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-around',
            }}>
                <Icon name='edit' color="#989898" />
                <Icon name='delete' color="#989898" />
            </Item>
        </ItemContainer>
    )
}
